import Vue from 'vue'
import Router from 'vue-router'
import helloWorld from '@/components/HelloWorld'
import index from '@/pages/index'

Vue.use(Router)

export default new Router({
//  mode: 'history',
  routes: [{
    path: '/',
    name: 'index',
    component: index
  }, {
    path: '/helloWorld',
    name: 'helloWorld',
    component: helloWorld
  },{
    path: '/a',
    name: 'helloWorld',
    component: helloWorld
  },{
    path: '/b',
    name: 'helloWorld',
    component: helloWorld
  },{
    path: '/c',
    name: 'helloWorld',
    component: helloWorld
  },{
    path: '/d',
    name: 'helloWorld',
    component: helloWorld
  }]
})



