<template>
  <div id="app">
    <div class="tourism-content">
      <el-header style="width: 1348px; height: 122px; padding:0;" class='top-header'>
        <el-row>
          <el-col :span="2" :offset="2">
            <div class="grid-content">
              <img style="width: 70px;    margin: 5px;" src="./assets/logo1.png">
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content text-shadow">
              e文化旅游
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content search-content">
              <el-input placeholder="好玩的...." prefix-icon="el-icon-search">
              </el-input>
            </div>
          </el-col>
          <el-col :span="8" :offset="4" class="el-col-phone">
            <div class="grid-content">
              <i class="el-icon-phone-outline"></i> 24小时服务热线：
              <span class="tel">123-456-7890</span>
            </div>
          </el-col>
        </el-row>
        <el-row class="el-row-header">
          <el-col :span="16" :offset="2">
            <div class="grid-content">
              <el-menu background-color="#4fc08d" text-color="#fff" active-text-color="#8b008b" :default-active="activeIndex" class="el-menu-header" mode="horizontal" @select="handleSelect" :router="true">
                <el-menu-item v-for='list in menuData' :key="list.id" :index="list.href">{{list.title}}
                </el-menu-item>
              </el-menu>
            </div>
          </el-col>
          
        </el-row>
      </el-header>
      <div class="container-wrap">
      <el-container class='main-content' style="width: 1270px;">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </el-container>
      </div>
      <el-footer style="width: 1348px; height: 80px;" class='bottom-footer'>
        <p>© 2018 finish zhangyu</p>
      </el-footer>
    </div>
  </div>
</template>
<script>
export default {
  name: 'app',
  data() {
    return {
      activeIndex: './',
      menuData: [{
        id: '1',
        title: '一日游',
        href: './'
      }, {
        id: '2',
        title: '三日游',
        href: '/helloWorld'
      }, {
        id: '3',
        title: '果农特色游',
        href: '/a'
      }, {
        id: '4',
        title: '渔乡特色游',
        href: '/b'
      }, {
        id: '5',
        title: '攻略',
        href: '/c'
      }, ]
    }
  },
  mounted: function() {
    const h = this.$createElement;
    this.$notify({
      title: '2018新年出游计划',
      message: h('div', { style: 'color: red;font-weight: bold;font-size: 30px;' }, '全场最高立减五百元'),
      duration: 50000
    });
  },
  methods: {
    handleSelect(key, keyPath) {
      this.activeIndex = key;
      console.log(key, keyPath)
    }
  }
}

</script>
<style>
* {
  margin: 0;
  padding: 0;
}

html,
body,
#app,
.tourism-content {
  width: 100%;
  height: 100%;
  background: #f0f2f5;
}

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}

ul>li {
  list-style-type: none;
}

.top-header {
  background: #fff;
}

.bottom-footer {
  background: #e3e4e8;
  line-height: 80px;
  color: #8a8686;
  text-align: center;
}

.main-content {
  margin: 0 auto;
  opacity: 1;
}
.container-wrap{
  background-image: url('./assets/bg1.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 1;
}
.top-header .el-row-header {
  background-color: #4fc08d;
 height: 40px;
}

.top-header .el-menu-header {
  border-bottom: 0;
  height: 40px;
}

.top-header .el-menu-header .el-menu-item {
  font-size: 12px;
  height: 40px;
  line-height: 40px;
  margin-left: 30px;
}

.top-header .text-shadow {

    color: lightblue;
    font-size: 40px;
    /* line-height: 63px; */
        line-height: 80px;
}

.top-header .el-col-phone {
  height: 90px;
  line-height: 90px;
  font-size: 25px;
}

.top-header .tel {
  font-family: "微锟斤拷锟脚猴拷";
  color: #C00;
}

.search-content {
  height: 60px;
  line-height: 90px;
}

.el-notification__group {
  cursor: default;
}

</style>



